.main {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 16px;
}

.mainCode {
  font-family: monospace;
  font-size: 1rem;
  color: #d63384;
  word-wrap: break-word;
}

.img {
  max-height: 60vh;
  object-fit: cover;
}
